:root {
  box-sizing: border-box;
  font-size: 1 em; }

body {
  background: #E5E8F1;
  border: 0.5vw solid #0B3142;
  color: #161925;
  display: grid;
  font-family: "Poppins", sans-serif;
  grid-template-columns: 1fr 8fr 1fr;
  max-width: 72em;
  min-width: 29.2em;
  margin: 0 auto; }
  body > section {
    grid-column: 2;
    min-width: 29.2em; }

.contact {
  background: #0B3142;
  padding: 5em 0; }

.contact-empty-left {
  grid-column: 1;
  background: #0B3142;
  width: auto; }

.contact-empty-right {
  grid-column: 3;
  background: #0B3142;
  width: auto;
  z-index: -1; }

.footer {
  background: #0B3142;
  font-size: 0.5rem;
  grid-column: 1 / span 3;
  text-align: right; }
  .footer div a {
    color: #29339B; }

.me {
  line-height: 2.8rem;
  margin-bottom: 10em; }
  .me__email .me__link {
    border-bottom: 0.1em solid #29339B;
    color: #29339B;
    padding-bottom: 0.4em;
    text-decoration: none; }
  .me__link {
    color: #29339B;
    font-style: normal; }
  .me__text {
    font-size: 1rem;
    line-height: 1.8rem;
    width: 63%; }

.my-projects {
  background: #FFFBE1;
  padding: 5em 0; }
  .my-projects__description {
    margin: 0;
    margin-top: .5em; }
  .my-projects__item {
    align-items: flex-start;
    align-self: auto;
    background: #E5E8F1;
    border-radius: 0.5em;
    box-shadow: 0.18em 0.18em rgba(0, 0, 0, 0.6);
    display: flex;
    flex-basis: 20em;
    flex-flow: column nowrap;
    flex-grow: inherit;
    justify-content: space-between;
    margin-bottom: .5em;
    min-width: 43%;
    padding: 1em; }
  .my-projects__link {
    color: #161925;
    font-style: normal;
    font-weight: bold;
    margin-top: .5em;
    text-decoration: none; }
  .my-projects__list {
    align-content: space-between;
    align-items: stretch;
    justify-items: baseline;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    list-style-type: none;
    margin: 0;
    padding: 0; }
  .my-projects__title {
    border-bottom: 0.07em solid #0B3142;
    margin: 0;
    padding-bottom: 0.53em; }

.my-projects-empty-left {
  grid-column: 1;
  background: #FFFBE1;
  width: auto; }

.my-projects-empty-right {
  grid-column: 3;
  background: #FFFBE1;
  width: auto;
  z-index: -1; }

.my-skills {
  margin-bottom: 5em; }
  .my-skills__title {
    border-bottom: 0.07em solid #FFFBE1;
    margin: 0;
    padding-bottom: 0.53em; }

.skill-list {
  align-items: flex-start;
  align-content: space-between;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between; }
  .skill-list__item {
    flex: 1 1 auto;
    line-height: 2em;
    margin-top: 0.17em;
    max-width: 50%;
    min-width: 12em; }
    .skill-list__item::before {
      color: #FFFBE1;
      content: "• "; }
  .skill-list__items {
    align-items: flex-start;
    align-content: space-between;
    display: flex;
    flex: 1 6 auto;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    list-style: none;
    max-width: 50%;
    min-width: 20em;
    padding: 0; }
  .skill-list__title {
    flex: 1 1 auto;
    line-height: 2rem;
    min-width: 12em; }
